<template>
    <div class="page-content">
        <div class="content-nav">
             &gt; 
        </div>
        <table class="listtable">
            <caption>:</caption>
            <tbody>
            <tr class="listheader">
                <th></th>
                <th></th>
                <th></th>
            </tr>
            <tr v-for="info in meetingInfo">
                <td>{{info.type}}</td>
                <td>{{info.desc}}</td>
                <td>
                    <router-link :to="{ name: info.type, params: { groupId: info.groupId }}"></router-link>
                </td>
            </tr>
            </tbody></table>
    </div>
</template>

<script>
    import axios from 'axios'
    import trace from "../js/logging";
    export default {
        name: 'query-meeting-content',
        data () {
            return {
                meetingInfo: []
            }
        },
        beforeRouteEnter(to, from, next) {
            axios.get(`/webmedia/ptt/group/`)
                .then( response => {
                    next( vm => {
                        trace("query:\r\n");
                        trace(response.data);
                        vm.meetingInfo = response.data.groupInfo;
                    });
                })
                .catch( error => {
                    console.log(error);
                });
        }

    }
</script>

<style scoped>
/*    .page-content{
        width:830px;
        float:right;
    }
    .page-content .content-nav{
        padding:0 0 10px 0;
    }*/
</style>



